// company logos
import linkedInLogo from "../../../../Asset/logos/linkedin.png"
import githubLogo from "../../../../Asset/logos/github.png"
import hiLogo from "../../../../Asset/hi.png"

const StringList = [
	"3Xo153w",
	"tQqH5Ce",
	"2ty4cr",
	"oj9D5x5",
	"YqI4jlAde1S",
	"whwDYjA",
	"SXgGewp0GC",
	"So8idXQee",
	"Sof7KReut",
	"SoftoxtW4uaHdS",
	"SoftwyA75S",
	"SoftwarWVw5W",
	"SoftwarCxi3J",
	"Software3vo0x",
	"Software N2pH",
	"Software EIbJ",
	"Software EnjPsS",
	"Software EngFd",
	"Software EngiSHaP",
	"Software Engind",
	"Software EngineE",
	"Software Enginee",
	"Software Engineer",
]

const Intro = () => {
	const [isHovered, setIsHovered] = useState(false)
	const [testString, setTestString] = useState("")

	useEffect(() => {
		let count = 0
		const interval = setInterval(() => {
			setTestString(StringList[count])
			if (++count >= StringList.length) clearInterval(interval)
		}, 80)

		return () => clearInterval(interval)
	}, [])

	const handleDownload = () => {
		const url = window.location.origin + "/TingchaoXu_Resume.pdf"
		const link = document.createElement("a")
		link.href = url
		link.setAttribute("download", "TingchaoXu_Resume.pdf")
		document.body.appendChild(link)
		link.click()
		document.body.removeChild(link)
	}

	const handleMouseEnter = () => {
		setIsHovered(true)
	}

	const handleMouseLeave = () => {
		setIsHovered(false)
	}

	return (
		<section className="introContent">
			<div className="describeSection">
				<p className="text1 js-scroll slide-top">
					{testString}
					<img className="hiLogo" src={hiLogo} alt="hi" />
				</p>

				<p>{"Hi, my name is Tingchao (William) Xu."}</p>
				<p>A passionate Senior Software Engineer based in CA.</p>
				<div className="companyLogos">
					<div
						className="logoBox"
						onMouseEnter={handleMouseEnter}
						onMouseLeave={handleMouseLeave}
					>
						<img
							className="companyLogo"
							src={resumeLogo}
							alt="Resume"
							onClick={handleDownload}
						/>
						<p>{isHovered ? "Download" : "Resume"}</p>
					</div>

					<div className="logoBox">
						<a
							href="https://www.linkedin.com/in/tingchao-xu-2b49a491/"
							target="_blank"
							rel="noopener noreferrer"
						>
							<img
								className="companyLogo"
								src={linkedInLogo}
								alt=""
							/>
						</a>
						<p>LinkedIn</p>
					</div>

					<div className="logoBox">
						<a
							href="https://github.com/WilliamXu-Infinity"
							target="_blank"
							rel="noopener noreferrer"
						>
							<img
								className="companyLogo"
								src={githubLogo}
								alt=""
							/>
						</a>
						<p>Github</p>
					</div>
				</div>
			</div>
			<img className="selfPhoto" src={selfImg} alt="" />
		</section>
	)
}

export default Intro
